<template lang="html">
  <footer class="footer" @click.stop='gotoRouter'>
    <mt-tabbar v-model="selected" fixed>
    <mt-tab-item id="">
        
    </mt-tab-item>
    <mt-tab-item id="">
      
    </mt-tab-item>
    <mt-tab-item id="">
      
    </mt-tab-item>
    <mt-tab-item id="">
      
    </mt-tab-item>
  </mt-tabbar>
  </footer>
</template>

<script>
export default {
  methods: {
    gotoRouter() {
      this.$router.push({
        name: this.selected
      })
    }
  },
  data() {
    return {
      selected: ''
    }
  },
  mounted() {
    let Rname = this.$route.name;
    switch (Rname) {
      case '':
        this.selected = '';
        break;
      case '':
        this.selected = '';

        break;
      case '':
        this.selected = '';

        break;
      case '':
        this.selected = '';
        break;
    }
  }

}
</script>

<style lang="less" scoped>
.mint-tab-item-label:hover {
  color: #333;
}
.mint-tab-item {
  padding: 24px 0px;
}
.mint-tab-item-icon {
  display: none;
}
.mint-tab-item-label {
  font-size: 14px !important;
}
.footer {
  .mint-tabbar {
    background-color: #fff;
    background-image: none;
    box-shadow: 0 0 2.2vw 0 hsla(0, 6%, 50%, 0.13);
    -webkit-box-shadow: 0 0 2.2vw 0 hsla(0, 6%, 50%, 0.13);
  }
}
</style>



